<template>
  <el-card class="box-card">
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>购买情况</el-breadcrumb-item>
      <el-breadcrumb-item>按教师查询</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="30">
      <el-col :span="8">
        <!-- 输入搜索功能 -->
        <el-input placeholder="请输入后搜索" v-model="serachKey" autofocus="true" clearable>
          <el-button slot="append" icon="el-icon-search" @click="handleSearch(serachKey)"></el-button>
        </el-input>
      </el-col>
    </el-row>

    <!-- 表格 -->
    <el-table
      :data="tableData.slice((tableRelevant.pageNow-1)*(tableRelevant.pageSize),(tableRelevant.pageNow)*(tableRelevant.pageSize))"
      style="width: 110%"
      border
      row-key
      :header-cell-style="{color:'#606266'}"
      class="booktable"
      max-width="250"
    >
      <!-- 行/列 -->
      <el-table-column prop="name" label="书名" align="center"></el-table-column>
      <el-table-column prop="zuozhe" label="作者" align="center"></el-table-column>
      <el-table-column prop="chubanshe" label="出版社" align="center"></el-table-column>
      <el-table-column prop="money" label="价格" align="center"></el-table-column>
      <el-table-column prop="shuliang" label="数量" align="center"></el-table-column>
      <el-table-column prop="teacher" label="购买人" align="center"></el-table-column>
    </el-table>
    <!-- 分页 -->
    <el-pagination
      class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="tableRelevant.pageNow"
      background
      :page-sizes="[1, 3, 5, 10]"
      :page-size="tableRelevant.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length"
    ></el-pagination>
  </el-card>
</template>

<script>
import { BookList, Mock } from "@/request/api.js";
export default {
  data() {
    return {
      serachKey: "", //搜索关键字
      tableRelevant: {
        pageNow: 1, //当前页码
        pageSize: 5, //每页数据条数
        index: "" //表格行索引
      },
      tableData: [] //表格数据
    };
  },
  watch: {
    serachKey() {
      //监听搜索框内容是否为空
      if (this.serachKey == "") {
        this.getUsersInFo();
      }
    }
  },
  created() {
    this.getUsersInFo(); //开始渲染数据
  },
  methods: {
    getUsersInFo() {    //请求图书信息
        BookList().then(res => {
            if(res.data.code=='1'){
                // const data = res.data.book
                //   this.tableData = data
        console.log(res)
            }else{console.log('没有获取到图书信息')}
        }).catch(function(error){console.log(error)})
    },

    // getUsersInFo() {
    //   //请求图书信息
    //   Mock().then(res => {
    //     this.tableData = res.books;
    //   });
    // },
    handleSizeChange(newSize) {
      //监听pageSize(每页数据条数)改变的事件
      this.tableRelevant.pageSize = newSize;
    },
    handleCurrentChange(newNow) {
      //监听pageNow(当前页码)改变的事件
      this.tableRelevant.pageNow = newNow;
    },
    handleSearch(serachKey) {
      //根据搜索关键字筛选符合出要求的数据内容
      const mineDatas = [];
      this.tableData.forEach((value, index) => {
        if (
          value.name.indexOf(serachKey) > -1 ||
          value.zuozhe.indexOf(serachKey) > -1 ||
          value.chubanshe.indexOf(serachKey) > -1
        ) {
          mineDatas.push(value);
        }
      });
      this.tableData = mineDatas; //重新赋值
    }
  }
};
</script>

<style scoped lang='stylus'>
.bookcar {
  font-size: 200%;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
</style>