<template>
  <el-card class="box-card">
    <!-- 面包屑导航 -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="breadcrumb">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>图书管理</el-breadcrumb-item>
      <el-breadcrumb-item>图书列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row :gutter="30">
      <el-col :span="8">
        <!-- 输入搜索功能 -->
        <el-input placeholder="请输入后搜索" v-model="serachKey" autofocus="true" clearable>
          <el-button slot="append" icon="el-icon-search" @click="handleSearch(serachKey)"></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <!-- 添加书籍功能 -->
        <el-button type="primary" @click="handleAddBooks" v-if="addBook">添加书籍</el-button>
      </el-col>
    </el-row>

    <!-- 表格 -->
    <el-table
      :data="tableData.slice((tableRelevant.pageNow-1)*(tableRelevant.pageSize),(tableRelevant.pageNow)*(tableRelevant.pageSize))"
      style="width: 110%"
      border
      row-key
      :header-cell-style="{color:'#606266'}"
      class="booktable"
      max-width="250"
    >
      <!-- 行/列 -->
      <el-table-column prop="name" label="书名" align="center"></el-table-column>
      <el-table-column prop="zuozhe" label="作者" align="center"></el-table-column>
      <el-table-column prop="chubanshe" label="出版社" align="center"></el-table-column>
      <el-table-column prop="money" label="价格" align="center"></el-table-column>
      <el-table-column prop="shuliang" label="数量" align="center" v-if="btnBs"></el-table-column>
      <el-table-column prop="something" label align="center" width="200" v-if="btnSome">
        <template slot-scope="scope">
          <el-button
            type="primary"
            v-show="btnAdd"
            icon="el-icon-circle-plus-outline"
            circle
            @click="handleCar(scope.$index,scope.row)"
          ></el-button>
          <el-button
            type="primary"
            v-show="btnGai"
            icon="el-icon-edit"
            circle
            @click="handleTrue(scope.$index,scope.row)"
          ></el-button>
          <el-button
            type="danger"
            v-show="btnEdd"
            icon="el-icon-delete"
            circle
            @click="handleDelete(scope.$index,scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 添加书籍对话框 -->
    <el-dialog
      title="添加书籍"
      :visible="addBookVisible"
      width="30%"
      :before-close="handleAddBookFalse"
      @close="EditBookData"
    >
      <el-form
        :model="addUsers"
        :rules="addUsersRules"
        label-width="70px"
        class="demo-ruleForm"
        ref="addUsers"
      >
        <el-form-item label="书名" prop="addBookname">
          <el-input v-model="addUsers.addBookname" placeholder="请输入书籍名称"></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="addBooktitle">
          <el-input v-model="addUsers.addBooktitle" placeholder="请输入书籍作者"></el-input>
        </el-form-item>
        <el-form-item label="出版社" prop="addBookprice">
          <el-input v-model="addUsers.addBookprice" placeholder="请输入书籍出版社"></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="addBooknum">
          <el-input v-model="addUsers.addBooknum" placeholder="请输入书籍价格"></el-input>
        </el-form-item>
        <el-form-item label="数量" prop="addBooknumber">
          <el-input v-model="addUsers.addBooknumber" placeholder="请输入书籍数量"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addBookVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleAddBookTrue">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 修改图书信息对话框 -->
    <el-dialog title="修改图书信息" :visible="dialogVisible" width="30%" :before-close="handleFalse">
      <el-form
        :model="editBook"
        :rules="editBookRules"
        label-width="70px"
        class="demo-ruleForm"
        ref="editBook"
      >
        <el-form-item label="书名" prop="bookname">
          <el-input v-model="editBook.bookname" placeholder="请输入书籍名称"></el-input>
        </el-form-item>
        <el-form-item label="作者" prop="remanname">
          <el-input v-model="editBook.remanname" placeholder="请输入书籍作者"></el-input>
        </el-form-item>
        <el-form-item label="出版社" prop="bookprice">
          <el-input v-model="editBook.bookprice" placeholder="请输入书籍出版社"></el-input>
        </el-form-item>
        <el-form-item label="价格" prop="booknum">
          <el-input v-model="editBook.booknum" placeholder="请输入书籍价格"></el-input>
        </el-form-item>
        <el-form-item label="数量" prop="booknumber">
          <el-input v-model="editBook.booknumber" placeholder="请输入书籍数量"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleEditTrue">确 定</el-button>
      </span>
    </el-dialog>
    <div
      class="bookcar"
      @click="drawer = true"
      type="primary"
      style="margin-left: 16px;"
      v-show="offCar"
    >
      <i class="el-icon-s-promotion"></i>
    </div>
    <el-drawer title="所选书籍" :visible.sync="drawer" :with-header="false">
      <div class="drtop">
        <div class="title">
          <el-page-header @back="drawer = false" content="所选书籍"></el-page-header>
          <br />
          <el-row :gutter="12">
            <el-col :span="24">
              <el-card shadow="hover" body-style="width:100%;">
                <el-table
                  stripe
                  max-height="450"
                  :data="allBook.filter(data => !search ||
                   data.name.toLowerCase().includes(search.toLowerCase())||
                   data.zuozhe.toLowerCase().includes(search.toLowerCase())||
                   data.chubanshe.toLowerCase().includes(search.toLowerCase())||
                   data.money.toLowerCase().includes(search.toLowerCase())
                   )"
                  style="width: 130%"
                >
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="书名" >
                          <span>{{ props.row.name }}</span>
                        </el-form-item>
                        <el-form-item label="作者">
                          <span>{{ props.row.zuozhe }}</span>
                        </el-form-item>
                        <el-form-item label="出版社">
                          <span>{{ props.row.chubanshe }}</span>
                        </el-form-item>
                        <el-form-item label="价格">
                          <span>{{ props.row.money }}</span>
                        </el-form-item>
                        <el-form-item label="数量">
                          <span>{{ props.row.shuliang }}</span>
                        </el-form-item>
                      </el-form>
                    </template>
                  </el-table-column>
                  <el-table-column label="书名" prop="name" align="center"></el-table-column>
                  <el-table-column label="价格" prop="money" align="center"></el-table-column>
                  <el-table-column align="right" fixed >
                    <template slot="header" slot-scope="scope">
                      <el-input v-model="search" size="mini" placeholder="输入关键字搜索" />
                    </template>
                    <template slot-scope="scope">
                      <button @click="handleEdd(scope.$index,scope.row)">-</button>
                      <span
                        style="width:30px;display:-moz-inline-box;display:inline-block;text-align:center; "
                      >{{scope.row.count}}</span>
                      <button @click="handleAdd(scope.row)">+</button>
                    </template>
                  </el-table-column>
                </el-table>
              </el-card>
            </el-col>
          </el-row>
          <div class="aeBook">
            <div class="div1">￥{{bookMoney()}}</div>
            <div class="div2">
              <el-button type="primary" round @click="teaBlist">提交</el-button>
              <el-button type="success" round @click="resAllBooks">清空</el-button>
            </div>
          </div>
        </div>
      </div>
    </el-drawer>
    <!-- 分页 -->
    <el-pagination
      class="pagination"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="tableRelevant.pageNow"
      background
      :page-sizes="[1, 3, 5, 10]"
      :page-size="tableRelevant.pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="tableData.length"
    ></el-pagination>
  </el-card>
</template>

<script>
import { teaBlist, Mock,SearchB } from "@/request/api.js";
export default {
  data() {
    return {
      // bookMoney:0,//所选图书价格
      search: "",
      allBook: [], //所选书籍列表
      drawer: false, //抽屉
      addBook: true, //添加书籍开关
      offCar: true, //购物车开关
      btnAdd: true, //添加开关
      btnEdd: true, //删除开关
      btnGai: true, //修改开关
      btnBs: true, //图书数量开关
      btnSome: true, //操作开关
      serachKey: "", //搜索关键字
      tableRelevant: {
        pageNow: 1, //当前页码
        pageSize: 5, //每页数据条数
        index: "" //表格行索引
      },
      tableData: [], //表格数据
      addBookVisible: false, //添加书籍的对话框开关
      dialogVisible: false, //图书信息修改的对话框开关
      addUsers: {
        //添加用户的表单的数据绑定
        addBookname: "",
        addBooktitle: "",
        addBookprice: "",
        addBooknum: "",
        addBooknumber: "" //表单验证对象
      },
      addUsersRules: {
        //添加书籍的表单验证规则对象
        addBookname: [
          { required: true, message: "请输入书籍名称", trigger: "blur" }
        ],
        addBooktitle: [
          { required: true, message: "请输入书籍作者", trigger: "blur" }
        ],
        addBookprice: [
          { required: true, message: "请输入书籍价格", trigger: "blur" }
        ],
        addBooknum: [
          { required: true, message: "请输入书籍出版社", trigger: "blur" }
        ],
        addBooknumber: [
          { required: true, message: "请输入书籍库存", trigger: "blur" }
        ]
      },
      editBook: {
        //修改书籍信息的表单的数据绑定
        bookname: "",
        remanname: "",
        bookprice: "",
        booknum: "",
        booknumber: "" //表单验证对象
      },
      editBookRules: {
        //修改书籍信息的表单验证规则对象
        bookname: [
          { required: true, message: "请输入书籍名称", trigger: "blur" }
        ],
        remanname: [
          { required: true, message: "请输入作者名称", trigger: "blur" }
        ],
        bookprice: [
          { required: true, message: "请输入书籍价格", trigger: "blur" }
        ],
        booknum: [
          { required: true, message: "请输入书籍出版社", trigger: "blur" }
        ],
        booknumber: [
          { required: true, message: "请输入书籍库存", trigger: "blur" }
        ]
      }
    };
  },
  watch: {
    serachKey() {
      //监听搜索框内容是否为空
      if (this.serachKey == "") {
        this.getUsersInFo();
      }
    }
  },
  created() {
    this.getUsersInFo(); //开始渲染数据
    var sess = window.sessionStorage.getItem("nav");
    if (sess == "menugly") {
      this.addBook = false;
      this.btnSome = false;
      this.offCar = false;
      this.btnAdd = false;
      this.btnEdd = false;
      this.btnGai = false;
      this.btnBs = true;
    } else if (sess == "menujs") {
      this.addBook = false;
      this.btnSome = true;
      this.offCar = true;
      this.btnAdd = true;
      this.btnEdd = false;
      this.btnGai = false;
      this.btnBs = false;
    } else if (sess == "menusd") {
      this.offCar = false;
      this.btnBs = true;
      this.btnAdd = false;
    }
  },
  methods: {
    getUsersInFo() {    //请求图书信息
        SearchB().then(res => {
            if(res.data.code=='1'){
                const data = res.data.list
                  this.tableData = data
        console.log(res)
            }else{console.log('没有获取到图书信息')}
        }).catch(function(error){console.log(error)})
    },

    // getUsersInFo() {
    //   //请求图书信息
    //   Mock().then(res => {
    //     this.tableData = res.books;
    //   });
    // },
    handleSizeChange(newSize) {
      //监听pageSize(每页数据条数)改变的事件
      this.tableRelevant.pageSize = newSize;
    },
    handleCurrentChange(newNow) {
      //监听pageNow(当前页码)改变的事件
      this.tableRelevant.pageNow = newNow;
    },
    handleSearch(serachKey) {
      //根据搜索关键字筛选符合出要求的数据内容
      const mineDatas = [];
      this.tableData.forEach((value, index) => {
        if (
          value.name.indexOf(serachKey) > -1 ||
          value.zuozhe.indexOf(serachKey) > -1 ||
          value.chubanshe.indexOf(serachKey) > -1
        ) {
          mineDatas.push(value);
        }
      });
      this.tableData = mineDatas; //重新赋值
    },
    handleCar(index, res) {
      //选择书籍
      if (this.allBook.indexOf(res) === -1) {
        this.$set(res, "count", "0");
        this.allBook.push(res);  
      }
    },
    bookMoney() {
      //计算所选图书价格
      var ipprice = 0;
      for (var i = 0; i < this.allBook.length; i++) {
        ipprice += this.allBook[i].money * this.allBook[i].count;
      }
      return ipprice;
    },
    handleEdd(index, res) {
      //计数器-
      res.count--;
      if (res.count < 0) {
        res.count = 0;
        this.$confirm("将此书籍从列表删除, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.allBook.splice(index, 1);
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消删除"
            });
          });
      }
    },
    handleAdd(res) {
      //计数器+
      res.count++;
    },
    teaBlist() {
      //提交数据
      let params = {
        allbook: this.allBook
      };
      this.$confirm("提交教师所选书籍列表, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "提交成功!"
          });
          teaBlist(params)
            .then(res => {
              console.log(res);
            })
            .catch(err => {
              console.log(err);
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消提交"
          });
        });
    },
    resAllBooks() {
      this.$confirm("清空教师所选书籍列表, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "清空成功!"
          });
          for (let i = this.allBook.length - 1; i >= 0; i--) {
            this.allBook.splice(i, 1);
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消清空"
          });
        });
    },
    handleAddBooks() {
      //书籍添加的对话框开启事件
      this.addBookVisible = true;
    },
    handleAddBookFalse() {
      //书籍添加的对话框关闭事件
      this.addBookVisible = false;
    },
    EditBookData() {
      this.$refs.addUsers.resetFields(); //关闭对话框的同时清除表单中已填写过的数据
    },
    handleAddBookTrue() {
      //书籍添加的对话框确认事件
      this.addBookVisible = false;
      this.$refs.addUsers.validate(valid => {
        //校验表单是否填写完整
        if (!valid) return;
        const addBook = {
          title: this.addUsers.addBooktitle,
          name: this.addUsers.addBookname,
          price: this.addUsers.addBookprice,
          number: this.addUsers.addBooknum
        };
        
        // axios.get('', {                  //添加书籍请求
        //     params: {
        //         book:this.addUsers.addBooktitle,
        //         zuozhe:this.addUsers.addBookname,
        //         password:this.addUsers.addBooknum,
        //         number: this.addUsers.addBookprice,
        //          shuliang:this.addUsers.addBooknumber
        //     }
        // }).then(res => {
        //     if(res.data.code=='1'){
        //         this.getUsersInFo()
        //         this.$message({
        //             message: '书籍添加成功',
        //             type: 'success'
        //         });
        //     }else{
        //         this.$message({
        //             message: '书籍添加失败',
        //             type: 'warning'
        //         });
        //     }
        // }).catch(function (error) { console.log(error) })

        this.$message.success("书籍添加成功!");
        setTimeout(() => {
          location.reload();
        }, 2000);
      });
    },
    handleTrue(index, res) {
      //行修改图书信息对话框获取索引值的点击事件
      this.dialogVisible = true;
      this.tableRelevant.index = index;
      // console.log(index)
    },
    handleFalse() {
      //行修改图书信息对话框的关闭事件
      this.dialogVisible = false;
    },
    handleEditTrue() {
      //行修改图书信息对话框的确认事件
      if (
        this.editBook.bookname == "" ||
        this.editBook.remanname == "" ||
        this.editBook.bookprice == "" ||
        this.editBook.booknum == ""
      ) {
        this.$message({
          message: "图书信息未填写完整!"
        });
      } else {
        // this.tableData[this.tableRelevant.index].title = this.editBook.bookname
        // this.tableData[this.tableRelevant.index].name = this.editBook.remanname
        // this.tableData[this.tableRelevant.index].price = this.editBook.bookprice
        // this.tableData[this.tableRelevant.index].number = this.editBook.booknum

        // axios.get('', {                        //修改用户信息请求
        //         params: {
        //         book:this.editBook.bookname,
        //         zuozhe:this.editBook.remanname,
        //         password:this.editBook.booknum,
        //         number: this.editBook.bookprice,
        //          shuliang:this.editBook.booknumber
        //         }
        //     }).then(res => {
        //         if(res.data.code=='1'){
        //             this.getUsersInFo()
        //             this.$message({
        //                 message: '书籍信息修改成功',
        //                 type: 'success'
        //             });
        //         }else{
        //             this.$message({
        //                 message: '书籍信息修改失败',
        //                 type: 'warning'
        //             });
        //         }
        //     }).catch(function (error) { console.log(error) })

        this.$message.success("书籍信息修改成功!");
      }
      this.dialogVisible = false;
      this.$refs.editBook.resetFields(); //关闭对话框后清空所填项
    },
    async handleDelete(index, res) {
      //行图书信息的删除事件
      const confirmResult = await this.$confirm(
        "此操作将永久删除该用户, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);
      // console.log(confirmResult)
      if (confirmResult !== "confirm") {
        return this.$message.info("已经取消删除操作");
      }
      this.$message.success("成功删除该用户");
      // axios.get('', {                                //删除用户
      //             params: {
      //                 num:index
      //             }
      //         }).then(res => {
      //             if(res.data.code=='1'){
      //                 this.getUsersInFo()
      //                 this.$message({
      //                     message: '书籍删除成功',
      //                     type: 'success'
      //                 });
      //             }else{
      //                 this.$message({
      //                     message: '书籍删除失败',
      //                     type: 'warning'
      //                 });
      //             }
      //         }).catch(function (error) { console.log(error) })
      // this.tableData.splice(index, 1)
    },
    handleClose() {
      this.drawer = false;
    }
  }
};
</script>

<style scoped lang='stylus'>


.demo-table-expand {
  font-size: 0;
}

.demo-table-expand label {
  width: 90px;
  color: #99a9bf;
}

.demo-table-expand .el-form-item {
  margin-right: 0;
  margin-bottom: 0;
  width: 90%;
}

.bookcar {
  font-size: 200%;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.3);
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 3;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.title {
  .el-card {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    align-items: center;

    .el-input-number {
      flex: 1;
    }
  }

  .aeBook {
    width: 100%;
    height: 50px;
    background-color: #EBEEF5;
    position: fixed;
    bottom: 0;
    display: flex;

    .div1 {
      width: 120px;
      height: 50px;
      line-height: 50px;
      text-align: center;
    }

    .div2 {
      line-height: 50px;
    }
  }
}
</style>