<template>
    <el-card class="box-card">
        <el-breadcrumb separator-class="el-icon-arrow-right" class='breadcrumb'>
            <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>用户管理</el-breadcrumb-item>
            <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-row :gutter="30">
            <el-col :span="8">
                <el-input placeholder="请输入后搜索" v-model="serachKey" autofocus=true clearable>
                    <el-button slot="append" icon="el-icon-search" @click='handleSearch(serachKey)'></el-button>
                </el-input>
            </el-col>
            <el-col :span='4'>
                <el-button type='primary' @click='handleAddBooks'>添加用户</el-button>
            </el-col>
        </el-row>

        <el-table
            :data="tableData.slice((tableRelevant.pageNow-1)*(tableRelevant.pageSize),(tableRelevant.pageNow)*(tableRelevant.pageSize))"
            style="width: 100%" border row-key :header-cell-style="{color:'#606266'}" class='booktable'>
            <el-table-column prop="name" label="姓名" align="center">
            </el-table-column>
            <el-table-column prop="shenfen" label="身份" align="center">
            </el-table-column>
            <el-table-column prop="number" label="手机号" align="center">
            </el-table-column>
            <el-table-column prop="password" label="密码" align="center">
            </el-table-column>
            </el-table-column>
            <el-table-column prop='something' label='操作' align='center'>
                <template slot-scope='scope'>
                    <el-button type="primary" icon="el-icon-edit" circle @click='handleTrue(scope.$index,scope.row)'>
                    </el-button>
                    <el-button type="danger" icon="el-icon-delete" circle @click='handleDelete(scope.$index,scope.row)'>
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="添加用户" :visible="addBookVisible" width="30%" :before-close="handleAddBookFalse"
            @close='EditBookData'>
            <el-form :model="addUsers" :rules="addUsersRules" label-width="70px" class="demo-ruleForm" ref='addUsers'>
                <el-form-item label="姓名" prop="addBookname">
                    <el-input v-model="addUsers.addBookname" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="身份" prop="addBooktitle">
                    <el-input v-model="addUsers.addBooktitle" placeholder="请输入（管理员/教师/书店）"></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="addBookprice">
                    <el-input v-model="addUsers.addBookprice" placeholder="请输入手机号"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="addBooknum">
                    <el-input v-model="addUsers.addBooknum" placeholder="请输入密码"></el-input>></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="addBookVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleAddBookTrue">确 定</el-button>
            </span>
        </el-dialog>

        <el-dialog title="修改用户信息" :visible="dialogVisible" width="30%" :before-close="handleFalse">
            <el-form :model="editBook" :rules="editBookRules" label-width="70px" class="demo-ruleForm" ref='editBook'>
                <el-form-item label="姓名" prop="bookname">
                    <el-input v-model="editBook.bookname" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="身份" prop="remanname">
                    <el-input v-model="editBook.remanname" placeholder="请输入身份（管理员/教师/书店）"></el-input>
                </el-form-item>
                <el-form-item label="手机号" prop="bookprice">
                    <el-input v-model="editBook.bookprice" placeholder="请输入手机号"></el-input></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="booknum">
                    <el-input v-model="editBook.booknum" placeholder="请输入密码"></el-input></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleEditTrue">确 定</el-button>
            </span>
        </el-dialog>

        <el-pagination class='pagination' @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="tableRelevant.pageNow" background :page-sizes="[1, 3, 5, 10]"
            :page-size="tableRelevant.pageSize" layout="total, sizes, prev, pager, next, jumper"
            :total="tableData.length">
        </el-pagination>
    </el-card>
</template>

<script>
    import { User,Mock,AddUser,ModifyUser,DeleteUser } from "@/request/api.js";
    export default {
        data() {
            return {
                serachKey: '',
                tableRelevant: {
                    pageNow: 1,
                    pageSize: 3,
                    index: ''
                },
                tableData: [],
                addBookVisible: false,
                dialogVisible: false,
                addUsers: {
                    addBookname: '', addBooktitle: '', addBookprice: '', addBooknum: ''
                },
                addUsersRules: {
                    addBookname: [
                        { required: true, message: '请输入姓名', trigger: 'blur' }
                    ], addBooktitle: [
                        { required: true, message: '请输入身份（管理员/教师/书店）', trigger: 'blur' }
                    ], addBookprice: [
                        { required: true, message: '请输入手机号', trigger: 'blur' }
                    ], addBooknum: [
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ]
                },
                editBook: {
                    bookname: '', remanname: '', bookprice: '', booknum: ''
                },
                editBookRules: {
                    bookname: [
                        { required: true, message: '请输入姓名', trigger: 'blur' }
                    ], remanname: [
                        { required: true, message: '请输入身份（管理员/教师/书店）', trigger: 'blur' }
                    ], bookprice: [
                        { required: true, message: '请输入手机号', trigger: 'blur' }
                    ], booknum: [
                        { required: true, message: '请输入密码', trigger: 'blur' }
                    ]
                }
            }
        }, watch: {
            serachKey() {
                if (this.serachKey == '') {
                    this.getUsersInFo()
                }
            }
        }, created() {
            this.getUsersInFo()
        }, methods: {

            getUsersInFo() {        //请求用户信息
                User().then(res => {
                    if(res.data.code=='1'){
                        const data = res.data.list
                    this.tableData = data
                    console.log(res)
                    }else{console.log('请求用户信息失败')}
                }).catch(function(error){console.log(error)})
            },

            // getUsersInFo() {
            //     Mock().then(res => {
            //         this.tableData = res.users
            //     })
            // },
            handleSizeChange(newSize) {
                this.tableRelevant.pageSize = newSize
            }, handleCurrentChange(newNow) {
                this.tableRelevant.pageNow = newNow
            }, handleSearch(serachKey) {
                const mineDatas = []
                this.tableData.forEach((value, index) => {
                    if (value.name.indexOf(serachKey) > -1 || value.password.indexOf(serachKey) > -1 || value.number.indexOf(serachKey) > -1 || value.shenfen.indexOf(serachKey) > -1) {
                        mineDatas.push(value)
                    }
                })
                this.tableData = mineDatas
            }, handleAddBooks() {
                this.addBookVisible = true
            }, handleAddBookFalse() {
                this.addBookVisible = false
            }, EditBookData() {
                this.$refs.addUsers.resetFields()
            }, handleAddBookTrue() {
                this.addBookVisible = false
                this.$refs.addUsers.validate(valid => {
                    if (!valid) return

                    let params={
                        name:this.addUsers.addBooktitle,
                        shenfen:this.addUsers.addBookname,
                        password:this.addUsers.addBooknum,
                        shenfen: this.addUsers.addBookprice
                    }
                        AddUser(params).then(res => {
                            if(res.data.code=='1'){
                                this.getUsersInFo()
                                this.$message({
                                    message: '用户添加成功',
                                    type: 'success'
                                });
                            }else{
                                this.$message({
                                    message: '用户添加失败',
                                    type: 'warning'
                                });
                            }
                        }).catch(function (error) { console.log(error) })

                    // this.tableData.unshift({ 'username': this.addUsers.addBooktitle, 'rolename': this.addUsers.addBookname, 'userrole': this.addUsers.addBookprice, 'email': this.addUsers.addBooknum })
                    this.$message.success('用户添加成功!')
                })
            }, handleTrue(index, res) {
                this.dialogVisible = true
                this.tableRelevant.index = index

            }, handleFalse() {
                this.dialogVisible = false
            }, handleEditTrue() {
                if (this.editBook.bookname == '' || this.editBook.remanname == '' || this.editBook.bookprice == '' || this.editBook.booknum == '') {
                    this.$message({
                        message: '用户信息未填写完整!'
                    })
                } else {
                    // this.tableData[this.tableRelevant.index].username = this.editBook.bookname
                    // this.tableData[this.tableRelevant.index].rolename = this.editBook.remanname
                    // this.tableData[this.tableRelevant.index].userrole = this.editBook.bookprice
                    // this.tableData[this.tableRelevant.index].email = this.editBook.booknum

                    let params={
                        name:this.editBook.bookname,
                        zuozhe:this.editBook.remanname,
                        password:this.editBook.booknum,
                        number: this.editBook.bookprice
                    }
                    ModifyUser(params).then(res => {
                            if(res.data.code=='1'){
                                this.getUsersInFo()
                                this.$message({
                                    message: '用户信息修改成功',
                                    type: 'success'
                                });
                            }else{
                                this.$message({
                                    message: '用户信息修改失败',
                                    type: 'warning'
                                });
                            }
                        }).catch(function (error) { console.log(error) })

                    // this.$message.success('用户信息修改成功!')
                }
                this.dialogVisible = false
                this.$refs.editBook.resetFields()
            }, async handleDelete(index, res) {
                const confirmResult = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).catch(err => err)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('已经取消删除操作')
                }
                // this.$message.success('成功删除该用户')
                let params={
                    num:index
                }
                DeleteUser(params).then(res => {
                            if(res.data.code=='1'){
                                this.getUsersInFo()
                                this.$message({
                                    message: '用户删除成功',
                                    type: 'success'
                                });
                            }else{
                                this.$message({
                                    message: '用户删除失败',
                                    type: 'warning'
                                });
                            }
                        }).catch(function (error) { console.log(error) })
            }
        }
    }
</script>

<style scoped lang='stylus'>

</style>