<template>
  <div>
    <el-card class="box-card">
      <span class="span">{{username}},欢迎登录教材征订系统!</span>
      <el-calendar v-model="value" class="calendar"></el-calendar>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: new Date(),
      username: ""
    };
  },
  created() {
    const username = window.sessionStorage.getItem("username");
    this.username = username;
  }
};
</script>

